* {
    font-family: 'Poppins', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
}

p {
    font-weight: 300;
    color: #111;
}

/* body {
    min-height: 1000px;
} */

section {
    padding: 100px;
}

/* BANNER */
.banner {
    position: relative;
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: url(../images/bg.jpg);
    background-size: cover;
}

.banner__content {
    max-width: 900px;
    text-align: center;
}

.banner__header {
    font-size: 5em;
    color: #fff;
}

.banner__text {
    font-size: 1em;
    color: #fff;
}

/* BUTTON */
.btn {
    font-size: 1em;
    color: #fff;
    background: #ff0157;
    display: inline-block;
    padding: 10px 30px;
    margin-top: 20px;
    text-transform: uppercase;
    text-decoration: none;
    letter-spacing: 2px;
    transition: 0.5s;
}

.btn:hover {
    letter-spacing: 6px;
}

.lanBtn {
    color: #fff;
    padding: 0.5;
    width: 100px;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    background: #ff0157;
    transition: background 1s, transform 0.5s;
}

.lanBtn:hover {
    background: black;
    transform: rotate(30deg);
}

/* NAV */
.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    padding: 40px 100px;
    z-index: 10000;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: 0.5s;
}

header.sticky {
    background: #fff;
    padding: 10px 100px;
    box-shadow: 0 5px 20px rgba(0,0,0,0.05);
}

.header__logo {
    color: #fff;
    font-weight: 700;
    font-size: 2em;
    text-decoration: none;
}

header.sticky .header__logo {
    color: #111;
}

.header__logo span {
    color: #ff0157;
}

.nav {
    position: relative;
    display: flex;
}

.nav__list {
    list-style: none;
    margin-left: 30px;  
}

.nav__list a {
    text-decoration: none;
    color: #fff;
    font-weight: 300;
    transition: color 0.3s;
}

header.sticky .nav__list a {
    color: #111;
}

.nav__list a:hover {
    color: #ff0157;
}

header.sticky .nav__list a:hover {
    color: #ff0157;
}

/* ABOUT US BLOCK */
.row {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.row .col50 {
    position: relative;
    width: 48%;
}

.text__title {
    color: #111;
    font-size: 2em;
    font-weight: 300;
}

.text__title span {
    color: #ff0157;
    font-weight: 700;
    font-size: 1.5em;
}

.image__box {
    position: relative;
    width: 100%;
    height: 100%;
}

.image__box .about__image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 20px;
}

/* PRODUCTS */
.title {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.products .content {
    display: flex;
    justify-content: center;
    flex-direction: row-reverse;
    flex-wrap: wrap;
    margin-top: 40px;
}

.products .content .box {
    width: 340px;
    margin: 20px;
    border: 15px solid #fff;
    box-shadow: 0 5px 35px rgba(0,0,0,0.08);
}

.products .content .box .imgBx {
    position: relative;
    width: 100%;
    height: 300px;
}

.products .content .box .imgBx img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 20px;
    transition: filter 0.3s;
}

.products .content .box .imgBx img:hover {
    filter: Sepia(100%);
}

.products .content .box .tex {
    padding: 15px 5px; 
}

.products .content .box .tex h3 {

    font-style: 400;
    color: #111;
}

/* FEATURES */
.features .content {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    flex-direction: row;
    margin-top: 40px;
}

.features .content .box {
    width: 250px;
    margin: 15px;
}

.features .content .box .imgBx {
    position: relative;
    width: 100%;
    height: 300px;
}

.features .content .box .imgBx img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 20px;
}

.features .content .box .imgBx img:hover {
    opacity: 1;
	-webkit-animation: flash 2s;
	animation: flash 2s;
}

@-webkit-keyframes flash {
	0% {
		opacity: .4;
	}
	100% {
		opacity: 1;
	}
}

@keyframes flash {
	0% {
		opacity: .4;
	}
	100% {
		opacity: 1;
	}
}

.features .content .box .text {
    padding: 15px 0 5px
}

.features .content .box .text h3 {
    font-weight: 400;
    color: #111;
}

/* TESTIMONIALS */
.testimonials {
    background: url(../images/bg2.jpg);
    background-size: cover;
}

.white .text__title, .white p {
    color: #fff;
}

.testimonials .content {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    flex-direction: row;
    margin-top: 40px;
}

.testimonials .content .box {
    width: 340px;
    margin: 20px;
    padding: 40px;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    transition: transform 0.5s;
}

.testimonials .content .box:hover {
    transform: scale(1.2);
}

.testimonials .content .box .imgBx {
    position: relative;
    width: 80px;
    height: 80px;
    margin-bottom: 20px;
    border-radius: 50px;
    overflow: hidden;
}

.testimonials .content .box .imgBx img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 20px;
}

.testimonials .content .box .text {
    text-align: center; 
}

.testimonials .content .box .text p {
    color: #666;
    font-style: italic;
}

.testimonials .content .box .text h3 {
    margin-top: 20px;
    font-size: 1em;
    color: #ff0157;
    font-weight: 600;
}

/* CONTACT */
.contact {
    background: url(../images/bg3.jpg);
    background-size: cover;
}

.contact__form {
    padding: 75px 50px;
    background: #fff;
    border-radius: 20px;
    box-shadow: 0 15px 50px rgba(0, 0, 0, 0.1);
    max-width: 500px;
    margin-top: 50px;
}

.contact__form h3 {
    color: #111;
    font-size: 1.2em;
    margin-bottom: 20px;
    font-weight: 500;
}

.contact__form .inputBox {
    position: relative;
    width: 100%;
    margin-bottom: 20px;
}

.contact__form .inputBox input,
.contact__form .inputBox textarea {
    width: 100%;
    border: 1px solid #555;
    border-radius: 20px;
    padding: 10px;
    color: #111;
    outline: none;
    font-size: 1rem;
    font-weight: 300;
    resize: none;
}

.contact__form .inputBox input[type='submit'] {
    font-size: 1em;
    color: #fff;
    background: #ff0157;
    display: inline-block;
    text-transform: uppercase;
    text-decoration: none;
    letter-spacing: 2px;
    transition: 0.5s;
    max-width: 100px;
    font-weight: 500;
    border: none;
    cursor: pointer;
}

.contact__form .inputBox input[type='submit']:hover {
    background: #5a001e;
}

@media screen and (max-width: 991px) {
    .header,
    .header.sticky {
        padding: 10px 20px;
    }
    .header .nav {
        display: none;
    }
    .header .nav.active {
        width: 100%;
        height: calc(100% - 68px);
        position: fixed;
        top: 68px;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        background: #fff;  
    }
    .header .nav li {
        margin-left: 0;
    }
    .header .nav li a {
        color: #111;
        font-size: 1.6em;
    }
    .menuToggle {
        position: relative;
        width: 40px;
        height: 40px;
        background: url(../images/menu.png);
        background-size: 30px;
        background-repeat: no-repeat;
        background-position: center;
        cursor: pointer;
    }
    .menuToggle.active {
        background: url(../images/close.png);
        background-size: 25px;
        background-repeat: no-repeat;
        background-position: center;
    }
    .header.sticky .menuToggle {
        filter: invert(1);
    }
    section {
        padding: 20px;
    }
    .banner__header {
        font-size: 3em;
    }
    .row {
        flex-direction: column;
    }
    .row .col50 {
        position: relative;
        width: 100%;
    }
    .image__box {
        height: 300px;
        margin-top: 20px;
    }
    .products .content {
        margin-top: 10px;
    }
    .products .content .box {
        margin: 10px;
    }
    .products .content .box .imgBx {
        height: 260px;
    }
    .title {
        text-align: center;
    }
    .text__title {
        font-size: 1.8em;
        line-height: 1.5em;
        margin-bottom: 15px;
        font-weight: 300;
    }
    .testimonials .content .box {
        margin: 10px;
        padding: 20px;
    }
    .contact__form {
        padding: 35px 40px;
        margin-top: 20px;
        margin-bottom: 30px;
    }
}

@media screen and (max-width: 480px) {
    .banner__content h2 {
        font-size: 2.2em;
    }
}